.content {
  width: 100%;
  height: calc(100vh - 50px);
  display: flex;
}

.canvasHolder {
  width: calc(100% - 350px);
}

.select-frequency {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  text-align: right;
  margin: 10px 25px;
  border: 1px solid gray;
  padding: 10px;
}

.title-and-tooltip {
  display: flex;
  justify-content: flex-end;
  align-items: center;

}

.title-and-tooltip > div {
  margin: 0 3px;
  color: var(--yellow-grey-4-hex);
}

div > svg{
  width: 20px;
  height: 20px;
}

.tooltipText {
  max-width: 150px;
  display: block;
  text-align: center;
}

.radio-list {
  display: flex;
  flex-flow: column;
  text-align: left;   
}

.btn-onoff {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  text-align: right;
}

.btn-onoff > .title-and-tooltip {
  height: 24px;
}

.btn-onoff > h4 {
  margin: 0;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 23px;
}
  
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--yellow-grey-2-hex);
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  left: 4px;
  bottom: 4px;
  background-color: var(--yellow-grey-5-hex);
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: var(--yellow-grey-4-hex);
}

input:focus + .slider {
  box-shadow: none;
}

input:checked + .slider:before {
  -webkit-transform: translateX(17px);
  -ms-transform: translateX(17px);
  transform: translateX(17px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.modal-aviso {
  padding: 10px 20px;
}

.modal-aviso > p {
  margin: 40px 0;
}